<template>
    <div class="myGoodsDetail">
        <div class="topTitle">
            <span class="name"></span>
            <span class="price"></span>
            <span class="number"></span>
            <span class="time">{{timeType}}</span>
            <span class="state">{{stateType}}</span>
        </div>
        <div class="myGoodsContent">
            <div class="contentDetail" v-for="g in detailGoods">
                <img :src="g.goodsPicture">
                <span class="name">{{g.goodsName}}</span>
                <span class="price">{{g.goodsPrice}}</span>
                <span class="number">{{g.repertory}}</span>
                <span class="time">{{g.uploadTime}}</span>

                <span class="state" v-if="title===''">{{g.goodsState}}</span>
                <a :class="title===''||title===''?'state':''" href="#" v-if="title!==''"></a>
                <span class="delete" v-if="title===''||title===''"></span>
                <span class="confer" v-if="title===''">
                    
                    <img :src="require('../../../assets/img/user/confer.png')">    
                </span>
            </div>
        </div>
    </div>
</template>

<script>
import {mapActions} from 'vuex'
import { setTimeout } from 'timers';

export default {
    data(){
        return{
            stateType:"",
            timeType:"",
            currentPage:0
        }
    },
    props:["title","detailGoods","pIndex"],
    methods:{
        ...mapActions("user",["getGoods"])
    },
    mounted(){
        let url;
        if(this.title===""){
            this.stateType="";
            this.timeType=""
            url="/ants/student/myTradingSituation"
        }else if(this.title===""||this.title===""){
            this.stateType="";
            this.timeType=""
            url="/ants/trade/myTradeGoods"
            if(this.title===""){
                url="/ants/tradeCase/tradingCase"
            }
        }else if(this.title===""){
            this.stateType="";
            this.timeType=""
            url="/ants/sell/mySellGoods"
        }
        let type=0;
        if(this.pIndex==0||this.pIndex==1){
            type=this.pIndex+1
        }else if(this.pIndex==2){
            type=4
        }else if(this.pIndex==3){
            type=3
        }
        let data={
            url:url,
            type:type,
            cPage:this.currentPage,
            title:this.title
        }
        this.getGoods(data);
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/less/define.less';

.myGoodsDetail{
    width: 800px;
    margin: 4px 28px;
    .topTitle{
        padding-top: 12px;
        padding-bottom: 11px;
        margin-bottom: 10px;
        background-color: @topicDeepBColor;
        color: #fff;
        .name{
            margin-left: 120px;
        }
        .price{
            margin-left: 102px;
        }
        .number{
            margin-left: 70px;
        }
        .time{
            margin-left: 87px;
        }
        .state{
            margin-left: 86px;
        }
    }
    .myGoodsContent{
        .contentDetail{
            margin-top: 10px;
            display: flex;
            align-items: center;
            >img{
                @imgLength:100px;
                width:@imgLength;
                height: @imgLength;
            }
            span{
                display: inline-block;
            }
            .name{
                margin-left: 20px;
                width: 110px;
            }
            .price{
                width: 52px;
                margin-left: 30px;
            }
            .number{
                width: 40px;
                margin-left: 58px;
                text-align: center;
            }
            .time{
                width: 121px;
                margin-left: 72px;
            }
            .state{
                width: 80px;
                margin-left: 40px;
                text-align: center;
            }
            .delete,
            .confer{
                color: @topicDeepRColor;
                cursor: pointer;
            }
            .confer{
                margin-left: 54px;
                img{
                    transform: translateY(4px);
                }
            }
            a{
                width: 72px;
                font-size: 14px;
                color:@topicDeepBColor;
            }
        }
    }
}
</style>
