const namespaced = true;
const state = {
    ws: "",
    url: "ws://127.0.0.1:8080/chat?",
    myId: '', // id
    userList: [{
        contactor: 171543114,
        contactorAvatar: require("../../assets/img/index/antsLogo.png"),
        contactorName: 'abc',
        latestTime: '2019/8/10'
    }, {
        contactor: 171543113,
        contactorAvatar: require("../../assets/img/index/antsLogo.png"),
        contactorName: '111',
        latestTime: '2019/7/10'
    }, {
        contactor: 171543115,
        contactorAvatar: require("../../assets/img/index/antsLogo.png"),
        contactorName: '222',
        latestTime: '2019/6/20'
    }, {
        contactor: 171543116,
        contactorAvatar: require("../../assets/img/index/antsLogo.png"),
        contactorName: '333',
        latestTime: '2019/5/10'
    }],
    chooseUser: {
        contactor: 171543114,
        contactorAvatar: require("../../assets/img/index/antsLogo.png"),
        contactorName: 'abc',
        latestTime: '2019/8/10'
    },
    // 
    // identify false true
    // type 0   1
    newsList: [{
        identify: false,
        content: ',',
        type: 0
    }, {
        identify: true,
        content: '~',
        type: 0
    }, {
        identify: false,
        content: '',
        type: 0
    }, {
        identify: false,
        content: '',
        type: 0
    }, {
        identify: false,
        content: require("../../assets/img/index/antsLogo.png"),
        type: 1
    }, {
        identify: false,
        content: '',
        type: 0
    }, ]
}
const getters = {
    getUserList(state) {
        return state.userList
    },
    getChooseUser(state) {
        return state.chooseUser
    },
    getNewsList(state) {
        return state.newsList
    }
}
const mutations = {
    chooseChange(state, user) {
        state.chooseUser = user;
    },
    // 
    pushMsg(state, data) {
        state.newsList.push(data);
    },
    // 
    changeUserList(state, contactorlist) {
        // require
        // contactorlist.forEach((contactor) => {
        //     contactor.contactorAvatar = require(contactor.contactorAvatar)
        // })
        state.userList = contactorlist;
    },
    // 
    changeList(state, newsList) {
        state.newsList = newsList
    }
}

const actions = {
    // 
    chooseChange({ commit, state, dispatch }, user) {
        commit("chooseChange", user)
        console.log('data: ', data);
        let data = {
            type: 0,
            id: state.myId,
            business: state.chooseUser.contactor,
        }
        dispatch("onSend", { data, file: "" })
    },
    // websocket
    socketInit({ state, dispatch }, { id, business }) {
        let url = "";
        state.myId = id;
        if (!business) {
            url = `${state.url}id=${id}&business=${business}`;
        } else {
            url = `${state.url}id=${id}`;
        }
        if ('WebSocket' in window) {
            state.ws = new WebSocket(url);
            dispatch("onMessage")
        } else if ('MozWebSocket' in window) {
            state.ws = new MozWebSocket(url);
            dispatch("onMessage")
        } else {
            alert('WebSocket is not supported by this browser.');
            return;
        }
    },
    // 
    onMessage({ state, commit }) {
        console.log('state: ', state);
        state.ws.onmessage = function(event) {
            console.log("event:", event);
            let data = JSON.parse(event.data);
            if (data.type == 0) {
                // 
                console.log("")
            } else if (data.type == 1) {
                console.log(' ', data);
                // 
                let identify = false;
                if (data.id == state.myId) {
                    identify = true;
                }
                commit("pushMsg", { content: data.msg, identify: identify, type: 1 })
            } else if (data.type == 2) {
                // 
                console.log('', data);
                let identify = false;
                if (data.id == state.myId) {
                    identify = true;
                }
                commit("pushMsg", { content: data.msg, identify: identify, type: 2 })
            } else if (data.type == 4) {
                // 
                console.log('', data);
                let newsList = [];
                data.from.forEach((info, index) => {
                    let identify = false;
                    if (info.id == state.myId) {
                        identify = true;
                    }
                    // type:0 1
                    newsList[index] = { content: info.information, identify, type: info.type };
                })
                commit("changeList", newsList);
            } else if (data.type == 5) {
                console.log('data: ', data);
                commit('changeUserList', data.contactorlist)
            }
        }
    },
    // 
    onSend({ state }, { data, file }) {
        // 
        if (data.type === 1 || data.type === 0) {
            console.log(' ', data);
            data = JSON.stringify(data);
            state.ws.send(data)
        }
        // 
        if (data.type === 2) {
            let reader = new FileReader();
            reader.readAsArrayBuffer(file)
            reader.onload = function(e) {
                let blob = e.target.result
                let fileFinish = {
                    type: 3,
                    id: data.id,
                    business: data.business
                };
                // 
                blob = JSON.stringify(blob);
                fileFinish = JSON.stringify(fileFinish);
                state.ws.send(blob)
                state.ws.send(fileFinish)
            }
        }
    },
}

export default { state, getters, mutations, actions, namespaced }